.card {
  background: black;
  border-radius: 2px;
  height: 175px;
  margin: 1rem;
  position: relative;
  width: 175px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: center;
}

.img-container {
  margin: auto;
  width: 100%;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.wrapper {
  padding-top: 50px;
  /* background: #ECEFF1; */
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  text-shadow:
    -1px -1px 0 #000,
		1px -1px 0 #000,
		-1px 1px 0 #000,
		1px 1px 0 #000;
  color: #fcf800;
}

.atlogo {
  width: 600px;
}

@media only screen and (max-width: 580px) {
  .atlogo {
    width: 100%;
  }
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
  background-image: url("https://raw.githubusercontent.com/Gamlilorien/Memory-Game/master/public/images/wallpaper.jpg");
  background-repeat: none;
  background-color: #13dbff;
}

